<template>
  <div>
    <div></div>
    <v-text-field
      v-model="key"
      label=""
      required
    ></v-text-field>
    <v-file-input multiple v-model="formfile" label="File input"></v-file-input>
    <v-btn @click="sureBtn"></v-btn>
    <v-btn text small @click="sureBtn"></v-btn>
    <v-btn text small @click="getBtn"></v-btn>

    <v-flex>
      <v-btn @click="upload" depressed>
        Upload Image
      </v-btn>
      <input type="file" id="upload" ref="upload" @change="changeimg" accept=".jpg, .jpeg, .png">
    </v-flex>
    
  </div>
</template>

<script>
  import {
    // postActivity, // 
    postFile, // 
    getFileByKey, //  
  } from '../../../api/module/backend'
  export default {
    name: 'Filetest',

    data: () => ({
      key: '',
      formfile: '',
    }),

    // created() {
    // },

    computed: {
    },

    watch: {
     
    },

    methods: {
      sureBtn() {
        let formdata = new FormData()
        formdata.append('FormFile', this.formfile[0])
        formdata.append('Key', this.key)
        formdata.append('OwnerId', '08d9aeef-768e-4091-88a1-9971037f0375')
        console.log(formdata)
        postFile(formdata).then(res => {
          console.log(res)
        })
      },


      upload () {
        let uploadbtn = this.$refs.upload
        uploadbtn.click()
      },
      async changeimg (e) {
        let formData = new FormData()
        console.log(e.target.files[0])
        formData.append('file', e.target.files[0])
        // await myServiece.fun(formData)
        console.log(formData)
        console.log('send')
      },

      getBtn() {
        let fdata = {
          OwnerId: '08d9aeef-768e-4091-88a1-9971037f0375',
          Key: '1'
        }
        getFileByKey(fdata).then(res => {
          console.log(res)
          let url = window.URL.createObjectURL(res.data); //fileBlob
          console.log(url)
          let a = document.createElement("a"); 
          document.body.appendChild(a);
          //  let fileName=res.headers["content-disposition"].split(";")[1].split("=")[1];  //filenameheaders
          // let fileName = params.objectName // 
          a.href = url;
          a.download = ''; //
          a.click(); //  
          window.URL.revokeObjectURL(url);  //
        })
      }


    }

  }
</script>

<style scoped>
#upload{
  height: 0;
  width: 0;
  visibility: hidden;
}
</style>